@import '../../assets/styles/config.scss';

.bookPage {
   margin-top: 40px;
   box-sizing: border-box;

   &-top {
      display: flex;
      justify-content: space-between;

      @media only screen and (max-width: 768px) {
         flex-direction: column;
      }


      &__title {
         @include normalFont(500, 35px, 42px, #000);

         @media only screen and (max-width: 768px) {
            @include normalFont(500, 25px, 32px, #000);
         }

         span {
            @include normalFont(500, 16px, 19px, #000);
            opacity: 0.4;
            margin-left: 12px;
         }
      }

      &__button {
         cursor: pointer;
         padding: 10px 25px;
         background: #407BFF;
         border-radius: 10px;
         color: #fff;
         border: 1px solid #407BFF;
         outline: none;

         @media only screen and (max-width: 768px) {
            margin-left: 0;
            margin-right: 10px;
            margin-top: 20px;
         }

         &:hover {
            background: transparent;
            color: #407BFF;
         }

         margin-left: 10px;

         &.red {
            border: 1px solid red;
            background: red;

            &:hover {
               background: transparent;
               color: red;
            }
         }
      }
   }

   &-center {
      margin-top: 64px;

      @media only screen and (max-width: 768px) {
         margin-top: 34px;
      }

      &-row {
         display: flex;

         @media only screen and (max-width: 1023px) {
            flex-direction: column;
         }

         &-left {


            &.row2 {
               min-width: 495px;
               max-width: 495px;

               @media only screen and (max-width: 768px) {
                  min-width: 100%;
                  max-width: 100%;
               }
            }

            img {
               min-width: 495px;
               max-width: 495px;
               max-height: 768px;
               min-height: 768px;
               box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
               background: url(../../assets/Images/loading.gif);
               background-position: center;
               background-repeat: no-repeat;
               background-size: 50px 50px;

               @media only screen and (max-width: 768px) {
                  min-width: 100%;
                  max-width: 100%;
                  min-height: auto;
                  max-height: auto;
               }
            }

            &-box {
               @include space-between;
               margin-top: 20px;

               &.block {
                  flex-direction: column;
                  align-items: flex-start;
                  justify-content: flex-start;

                  .subtitle {
                     margin-top: 10px;
                  }
               }

               .title {
                  @include normalFont(normal, 14px, 17px, #000);
                  opacity: 0.4;
                  width: 45%;
               }

               .subtitle {
                  width: 45%;
                  @include normalFont(normal, 14px, 17px, #000);
               }
            }
         }

         &-right {
            margin-left: 60px;

            @media only screen and (max-width: 1023px) {
               margin-left: 0;
               margin-top: 60px;

            }

            @media only screen and (max-width: 768px) {
               margin-top: 34px;
            }



            &__title {
               @include normalFont(500, 35px, 42px, #000);

               @media only screen and (max-width: 768px) {
                  @include normalFont(500, 25px, 32px, #000);
               }
            }

            &__subtitle {
               @include normalFont(500, 18px, 22px, #000);
               margin-top: 30px;
               opacity: 0.4;
               display: block;
            }

            &__price {
               @include normalFont(500, 25px, 30px, #000);

               del {
                  margin-right: 20px;
                  opacity: 0.5;
               }
            }

            .buy {
               display: flex;
               align-items: center;
               justify-content: space-between;
               margin-top: 40px;
            }

            &__button {
               background: #F7F8F8;
               border-radius: 10px;
               border: 4px solid transparent;
               box-sizing: border-box;
               outline: none;
               padding: 15px 32px;
               cursor: pointer;
               position: relative;

               @media only screen and (max-width: 768px) {
                  padding: 8px 16px;
               }

               &.active {
                  background: #fe3905;
                  animation: w3-alert-loader 2s infinite;
                  box-sizing: border-box;
                  -webkit-box-sizing: border-box;
                  -moz-box-sizing: border-box;

                  svg {
                     fill: #fff;
                  }


                  &:hover {
                     border: 4px solid #fe3905;
                     background: transparent;

                     svg {
                        fill: #fe3905;
                     }
                  }
               }

               &:hover {
                  border: 4px solid #F7F8F8;
                  background: transparent;
               }

               svg {
                  width: 31px;
                  height: 31px;

                  @media only screen and (max-width: 768px) {
                    width: 21px;
                    height: 21px;
                  }
               }

               .w3-alert-loader {
                  width: 22px;
                  height: 22px;
                  -webkit-border-radius: 30px;
                  -moz-border-radius: 30px;
                  border-radius: 30px;
                  background: #fe3905;
                  animation: w3-alert-loader 2s infinite;
                  border: 2px solid var(--rightSideBarBg);
                  box-sizing: border-box;
                  -webkit-box-sizing: border-box;
                  -moz-box-sizing: border-box;
                  position: absolute;
                  z-index: 9;
                  display: block;
                  top: -15px;
                  right: -15px;
                  @include center;
                  color: #fff;

                  @media only screen and (max-width: 768px) {
                     width: 16px;
                     height: 16px;
                  }
               }



               @keyframes w3-alert-loader {
                  0.00% {
                     -webkit-box-shadow: 0 0 0 0 rgba(254, 57, 5, 0.7);
                     -moz-box-shadow: 0 0 0 0 rgba(254, 57, 5, 0.7);
                     box-shadow: 0 0 0 0 rgba(254, 57, 5, 0.7);
                  }

                  100% {
                     -webkit-box-shadow: 0 0 0 15px rgba(254, 57, 5, 0);
                     -moz-box-shadow: 0 0 0 15px rgba(254, 57, 5, 0);
                     box-shadow: 0 0 0 15px rgba(254, 57, 5, 0);
                  }
               }

               @-webkit-keyframes w3-alert-loader {
                  0.00% {
                     -webkit-box-shadow: 0 0 0 0 rgba(255, 155, 88, 0.7);
                     -moz-box-shadow: 0 0 0 0 rgba(255, 155, 88, 0.7);
                     box-shadow: 0 0 0 0 rgba(255, 155, 88, 0.7);
                  }

                  100% {
                     -webkit-box-shadow: 0 0 0 15px rgba(255, 155, 88, 0);
                     -moz-box-shadow: 0 0 0 15px rgba(255, 155, 88, 0);
                     box-shadow: 0 0 0 15px rgba(255, 155, 88, 0);
                  }
               }
            }

            &-about {
               margin-top: 60px;

               @media only screen and (max-width: 768px) {
                  margin-top: 30px;
               }

               &__title {
                  @include normalFont(600, 16px, 19px, #000);
               }

               &__text {
                  margin-top: 30px;
                  @include normalFont(normal, 14px, 26px, #000);
               }
            }

            &-discr {
               padding: 30px 0;
               box-sizing: border-box;
               margin-top: 30px;
               opacity: 0.3;
               border-top: 1px solid #000000;
               border-bottom: 1px solid #000000;
               @include normalFont(normal, 14px, 26px, #000);
            }

            &-feature {
               margin-top: 20px;

               &__title {
                  @include normalFont(600, 16px, 19px, #000);
               }

               ul {
                  padding-left: 15px;

                  li {
                     margin-top: 20px;
                     @include normalFont(normal, 14px, 26px, #0000009d);
                  }
               }
            }

            &-forwhom {
               margin-top: 50px;

               &__title {
                  @include normalFont(600, 16px, 19px, #000);
               }

               &__text {
                  @include normalFont(normal, 14px, 26px, #000);
                  opacity: 0.7;
                  margin-top: 30px;
               }
            }
         }
      }
   }
}