@import '../../assets/styles/config.scss';

.login {
   @include space-between;
   margin-top: 60px;

   @media only screen and (max-width: 1023px) {
      box-sizing: border-box;
   }

   &-left {
      @include column;
      width: 45%;
      

      @media only screen and (max-width: 1023px) {
         width: 50%;
      }

      @media only screen and (max-width: 768px) {
        width: 100%;
      }

      &__title {
         @include normalFont(600, 45px, 54px, #000);
         margin-bottom: 60px;
      }

      &__input {
         padding: 16px 0;
         width: 100%;
         margin-top: 40px;
         border: none;
         outline: none;
         border-bottom: 1px solid #000000;
         @include normalFont(500, 14px, 17px, #000);

         &::placeholder {
            color: #858577;
         }

      }

      &__button {
         margin-top: 60px;
         @include normalFont(600, 22px, 26px, #000);
         background: #F7F8F8;
         border-radius: 10px;
         padding: 27px 0;
         border: none;
         outline: none;
         cursor: pointer;

         &:hover {}
      }
   }

   &-right {
      width: 45%;

      @media only screen and (max-width: 768px) {
         display: none;
      }
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }

      @media only screen and (max-width: 1023px) {
         max-width: 45%;
      }
   }
}