@mixin center {
    display: flex;
    align-items: center;
    justify-content: center;
 }
 
 @mixin align-center {
    display: flex;
    align-items: center;
 }
 
 @mixin space-between {
    display: flex;
    justify-content: space-between;
 }
 
 @mixin space-between-center {
    display: flex;
    align-items: center;
    justify-content: space-between;
 }
 
 @mixin column {
    display: flex;
    flex-direction: column;
 }
 
 @mixin column-center {
    display: flex;
    align-items: center;
    flex-direction: column;
 }
 
 @mixin normalFont($weight, $size, $line, $color) {
    font-style: normal;
    font-weight: $weight;
    font-size: $size;
    line-height: $line;
    color: $color;
 }
 