@import '../../assets/styles/config.scss';

.header {
    @include space-between-center;
    padding: 10px 15px;
    box-sizing: border-box;


    .boxHeader {
        display: none;
        align-items: center;

        .button-search {
            margin: 0;
            margin-right: 20px;

        
        }

        @media only screen and (max-width: 768px) {
            display: flex;
        }
    }

    &-left {

        img {
            width: 70px;
            height: auto;
            object-fit: cover;

             @media only screen and (max-width: 768px) {
                width: 50px;
             }
        }
    }

    &-center {
        @include center;

        .mobile {
            display: none;
        }

        a {
            text-decoration: none;
            @include normalFont(500, 16px, 19px, #000);
            opacity: 0.5;
            margin: 0 15px;

            &:hover {
                opacity: 1;
            }

            &.active {
                opacity: 1;
            }
        }

        @media only screen and (max-width: 768px) {
            position: fixed;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            background: #fff;
            flex-direction: column;
            @include center;
            z-index: 2000;
            transform: translateX(-150%);
            transition: all ease 0.5s;
            
            &.active {
                transition: all ease 0.5s;
                transform: translateX(0);
            }

            a {
                margin-bottom: 20px
            }

            .mobile {
                display: flex;
                flex-direction: column;

                .button-login {
                    cursor: pointer;
                    @include center;
                    background: transparent;
                    border: none;
                    outline: none;
                    text-decoration: none;
                    color: #000;

                    span {
                        margin-right: 8px;
                    }

                    
                }


            }

        }
    }

    &-right {
        @include align-center;

       

        .button-login {
            cursor: pointer;
            @include center;
            background: transparent;
            border: none;
            outline: none;
            text-decoration: none;
            color: #000;
            @media only screen and (max-width: 768px) {
                display: none;
            }

            span {
                margin-right: 8px;
            }
        }

        
    }

     .button-search {
         @include center;
         background: transparent;
         border: none;
         outline: none;
         margin-right: 30px;
         cursor: pointer;
         position: relative;

         svg {
             width: 22px;
             height: 22px;
         }

         @media only screen and (max-width: 768px) {
             display: none;

             &.basket {
                 display: flex;
             }
         }

         .w3-alert-loader {
             width: 12px;
             height: 12px;
             -webkit-border-radius: 30px;
             -moz-border-radius: 30px;
             border-radius: 30px;
             background: #fe3905;
             animation: w3-alert-loader 2s infinite;
             transform: translate(20px, 20px);
             border: 2px solid var(--rightSideBarBg);
             box-sizing: border-box;
             -webkit-box-sizing: border-box;
             -moz-box-sizing: border-box;
             position: absolute;
             z-index: 9;
             display: block;
             top: -27px;
             left: -3px;
         }



         @keyframes w3-alert-loader {
             0.00% {
                 -webkit-box-shadow: 0 0 0 0 rgba(254, 57, 5, 0.7);
                 -moz-box-shadow: 0 0 0 0 rgba(254, 57, 5, 0.7);
                 box-shadow: 0 0 0 0 rgba(254, 57, 5, 0.7);
             }

             100% {
                 -webkit-box-shadow: 0 0 0 15px rgba(254, 57, 5, 0);
                 -moz-box-shadow: 0 0 0 15px rgba(254, 57, 5, 0);
                 box-shadow: 0 0 0 15px rgba(254, 57, 5, 0);
             }
         }

         @-webkit-keyframes w3-alert-loader {
             0.00% {
                 -webkit-box-shadow: 0 0 0 0 rgba(255, 155, 88, 0.7);
                 -moz-box-shadow: 0 0 0 0 rgba(255, 155, 88, 0.7);
                 box-shadow: 0 0 0 0 rgba(255, 155, 88, 0.7);
             }

             100% {
                 -webkit-box-shadow: 0 0 0 15px rgba(255, 155, 88, 0);
                 -moz-box-shadow: 0 0 0 15px rgba(255, 155, 88, 0);
                 box-shadow: 0 0 0 15px rgba(255, 155, 88, 0);
             }
         }

     }

    .burger {
        position: relative;
        width: 35px;
        height: 35px;
        @include center;
        z-index: 2001;

        

        &.active {
            span {
                position: absolute;
                width: 30px;
                height: 2px;
                background: transparent;
                

                &::before {
                    content: "";
                    position: absolute;
                    width: 30px;
                    height: 2px;
                    background: #000;
                    transform: rotate(45deg);
                    transition: all ease 0.5s;
                }
                
                &::after {
                    content: "";
                    position: absolute;
                    width: 30px;
                    height: 2px;
                    background: #000;
                    transform: rotate(-45deg);
                    transition: all ease 0.5s;
                }
            }
        }

        span {
            position: absolute;
            width: 30px;
            height: 2px;
            background: #000;

            &::before {
                content: "";
                position: absolute;
                width: 30px;
                height: 2px;
                background: #000;
                transform: translateY(-7px);
                transition: all ease 0.5s;
            }

            &::after {
                content: "";
                position: absolute;
                width: 30px;
                height: 2px;
                background: #000;
                transform: translateY(7px);
                transition: all ease 0.5s;
            }
        }
    }
}