@import "../../assets/styles/config.scss";

.newsSwiper-block {
    width: 100%;
    position: relative;
    height: calc(100vh - 93.2px);

    @media only screen and (max-width: 768px) {
        height: calc(100vh + 73.2px);
    }

    &-bg {
        position: absolute;
        width: 100%;
        height: calc(100vh - 73.2px);
        margin: 0 auto;
        @include center;

        
        @media only screen and (max-width: 768px) {
            &::before {
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                bottom: 0;
                right: 0;
                left: 0;
                z-index: 1;
                background: rgba(255, 255, 255, 0.584);

            }
        }



        img {
            position: absolute;
            width: 100%;
            height: 100%;
            object-fit: cover;

            @media only screen and (max-width: 768px) {
                object-position: right;
            }
        }
    }

    &-info {
        position: relative;
        padding-top: 185px;
        box-sizing: border-box;
        z-index: 2;

        @media only screen and (max-width: 1023px) {
            padding-top: 85px;
        }

        @media only screen and (max-width: 768px) {
            padding-top: 55px;
        }

        &__title {
            @include normalFont(normal, 65px, 80px, #000);

            @media only screen and (max-width: 1023px) {
                @include normalFont(bold, 45px, 60px, #000);

            }

            @media only screen and (max-width: 768px) {
                @include normalFont(bold, 35px, 50px, #000);
            }
        }

        &__subtitle {
            @include normalFont(normal, 28px, 38px, #000);
            opacity: 0.5;
            width: 50%;
            margin-top: 24px;

            @media only screen and (max-width: 1023px) {
                @include normalFont(bold, 18px, 28px, #000);

            }

            @media only screen and (max-width: 768px) {
                @include normalFont(bold, 15px, 25px, #000);
                width: 80%;
            }
        }

        &__button {
            margin-top: 50px;
            padding: 18px 60px;
            box-sizing: border-box;
            @include center;
            display: inline-flex;
            background: #407bff;
            border-radius: 10px;
            @include normalFont(500, 20px, 24px, #fff);
            text-decoration: none;

            @media only screen and (max-width: 1023px) {
                @include normalFont(bold, 15px, 19px, #fff);

            }

            @media only screen and (max-width: 768px) {
                padding: 12px 30px;
            }
        }
    }
}