.mySwiper {
   height: 520px;
   .swiper-pagination {
      // display: none;

      .swiper-pagination-bullet {
         width: 12px;
         height: 12px;
         background: #B0B0B0;
         opacity: 1;
         transition: all ease 0.5s;
         &.swiper-pagination-bullet-active {
            width: 30px;
            border-radius: 5px;
            background: #000;
         }
      }
   }
}