@import '../../assets/styles/config.scss';

.verifyLoader {
   position: fixed;
   left: 0;
   top: 0;
   bottom: 0;
   right: 0;
   @include center;
   background: rgba(0, 0, 0, 0.575);
   z-index: 1000;

   &-inner {
      @include center;
      min-width: 300px;
      min-height: 180px;
      background: #fff;
      position: relative;
      flex-direction: column;

      &__title {
        margin-top: 20px
      }
   }

   .checkMark {
      @include center;
      
   }

   .lds-ring {
      display: inline-block;
      position: relative;
      width: 88px;
      height: 88px;
      transform: translateY(-6px) translateX(-5px);
   }

   .lds-ring div {
      box-sizing: border-box;
      display: block;
      position: absolute;
      width: 88px;
      height: 88px;
      margin: 8px;
      border: 5px solid #FF5952;
      border-radius: 50%;
      animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
      border-color: #FF5952 transparent transparent transparent;
   }

   .lds-ring div:nth-child(1) {
      animation-delay: -0.45s;
   }

   .lds-ring div:nth-child(2) {
      animation-delay: -0.3s;
   }

   .lds-ring div:nth-child(3) {
      animation-delay: -0.15s;
   }

   @keyframes lds-ring {
      0% {
         transform: rotate(0deg);
      }

      100% {
         transform: rotate(360deg);
      }
   }

   .success-checkmark {
      width: 80px;
      height: 89px;
      margin: 0 auto;

      .check-icon {
         width: 80px;
         height: 80px;
         position: relative;
         border-radius: 50%;
         box-sizing: content-box;
         border: 4px solid #4caf50;

         &::before {
            top: 3px;
            left: -2px;
            width: 30px;
            transform-origin: 100% 50%;
            border-radius: 100px 0 0 100px;
         }

         &::after {
            top: 0;
            left: 30px;
            width: 60px;
            transform-origin: 0 50%;
            border-radius: 0 100px 100px 0;
            animation: rotate-circle 4.25s ease-in;
         }

         &::before,
         &::after {
            content: "";
            height: 100px;
            position: absolute;
            background: transparent;
            transform: rotate(-45deg);
         }

         .icon-line {
            height: 5px;
            background-color: #4caf50;
            display: block;
            border-radius: 2px;
            position: absolute;
            z-index: 10;

            &.line-tip {
               top: 46px;
               left: 14px;
               width: 25px;
               transform: rotate(45deg);
               animation: icon-line-tip 0.75s;
            }

            &.line-long {
               top: 38px;
               right: 8px;
               width: 47px;
               transform: rotate(-45deg);
               animation: icon-line-long 0.75s;
            }
         }

         .icon-circle {
            top: -4px;
            left: -4px;
            z-index: 10;
            width: 80px;
            height: 80px;
            border-radius: 50%;
            position: absolute;
            box-sizing: content-box;
            border: 4px solid rgba(76, 175, 80, 0.5);
         }

         .icon-fix {
            top: 8px;
            width: 5px;
            left: 26px;
            z-index: 1;
            height: 85px;
            position: absolute;
            transform: rotate(-45deg);
            background-color: transparent;
         }
      }
   }

   @keyframes rotate-circle {
      0% {
         transform: rotate(-45deg);
      }

      5% {
         transform: rotate(-45deg);
      }

      12% {
         transform: rotate(-405deg);
      }

      100% {
         transform: rotate(-405deg);
      }
   }

   @keyframes icon-line-tip {
      0% {
         width: 0;
         left: 1px;
         top: 19px;
      }

      54% {
         width: 0;
         left: 1px;
         top: 19px;
      }

      70% {
         width: 50px;
         left: -8px;
         top: 37px;
      }

      84% {
         width: 17px;
         left: 21px;
         top: 48px;
      }

      100% {
         width: 25px;
         left: 14px;
         top: 45px;
      }
   }

   @keyframes icon-line-long {
      0% {
         width: 0;
         right: 46px;
         top: 54px;
      }

      65% {
         width: 0;
         right: 46px;
         top: 54px;
      }

      84% {
         width: 55px;
         right: 0px;
         top: 35px;
      }

      100% {
         width: 47px;
         right: 8px;
         top: 38px;
      }
   }

}