@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap');

body {
    margin: 0;
    padding: 0;
    font-family: 'Lato', sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
}

.container {
    max-width: 1140px;
    width: 100%;
    padding: 0 15px;
    margin: 0 auto;
    
    @media only screen and (max-width: 1023px) {
        max-width: 1140px;
    }
}

.app {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &-header {
        position: fixed;
        left: 0;
        right: o;
        top: 0;
        width: 100%;
        height: max-content;
        z-index: 20;
        background: #fff;

        &.active {
            box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
        }

       

    }

    &-center {
        margin-top: 93.2px;

        @media only screen and (max-width: 768px) {
            margin-top: 73.2px;
        }
    }

    &-footer {
        background: #000;
        margin-top: 90px;
        
        &-aboutdev {
            padding: 10px 0;
            display: flex;
            align-items: center;
            justify-content: center;
            a {
                color: #000;
                text-decoration: none;

                span {
                    text-decoration: underline;
                    margin-right: 10px;
                }
            }
        }
    }
}