@import '../../../assets/styles/config.scss';

.fotoInput {
 @include space-between;
   width: 100%;

   
   

   &-left {
      width: 145px;
      height: 225px;
      @include center;
      background: #F8F8F8;
      border: 1px solid #407BFF;
      box-sizing: border-box;
      border-radius: 5px;
      overflow: hidden;

      
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }

   &-right {
      width: calc(100% - 165px);
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      &__subtitle {
         opacity: 0.3;
         font-size: 11px;
         line-height: 15px;
      }

     

      label {
         @include normalFont(500, 11px, 13px, #fff);
         display: flex;
         align-items: center;
         justify-content: center;
         padding: 10px 0;
         background: #407BFF;
         border: 1px solid #EEEEEE;
         box-sizing: border-box;
         border-radius: 5px;
         overflow: hidden;
         cursor: pointer;

         b {
            position: relative;
            transition: all 0.25s cubic-bezier(0.310, -0.105, 0.430, 1.400);
            z-index: 1;
            @include center;
            margin-left: 5px;
            transform: translateY(-220%);
         }

         span {
            position: relative;
            z-index: 2;
            transform: translateX(10px);
            transition: all 0.25s cubic-bezier(0.310, -0.105, 0.430, 1.400);
         }

         &:hover {

            span {
               transform: translateX(0);
               transition: all 0.25s cubic-bezier(0.310, -0.105, 0.430, 1.400);
            }

            b {
               transform: translateY(0);
               transition: all 0.25s cubic-bezier(0.310, -0.105, 0.430, 1.400);
            }
         }
         input {
            display: none;
         }
      }
   }
}