@import '../../assets/styles/config.scss';
.footer {
   @include space-between;
   padding: 75px 15px;
   box-sizing: border-box;

   &-items {
      &__logo {
         img {
            width: 70px;
            height: auto;
            object-fit: cover;
         }
      }

      @media only screen and (max-width: 768px) {
         display: none;

         &:first-child {
            display: flex;
         }
      }

      @include column;

      &__phone {
         @include align-center;
         @include normalFont(normal, 16px, 24px, #fff);
         letter-spacing: -0.01em;
         opacity: 0.6;
         margin-top: 20px;
         &--icon {
            @include center;
            margin-right: 12px;
         }

         &:first-child {
            margin-top: 30px;
         }
      }

      &-social {
         margin-top: 32px;
         @include align-center;

         a {
            @include center;
            margin-right: 12px;
            opacity: 0.5;
         }
      }

      &__title {
         @include normalFont(500, 20px, 24px, #fff);
      }

      ul {
         margin: 0;
         padding: 0;
         margin-top: 30px;
         
         li {
            margin: 0;
            padding: 0;
            @include normalFont(normal, 14px, 20px, #fff);
            opacity: 0.5;
            list-style: none;
            margin-top: 8px;
            a {
               text-decoration: none;
               color: #fff;

               &:hover {
                  text-decoration: underline;
               }
            }
         }
      }
   }
}