.notResult {
   position: absolute;
   width: 100%;
}
.empty-state {
   width: 100%;
   margin: 40px auto;
   background: #ffffff;
   box-shadow: 1px 2px 10px #e1e3ec;
   border-radius: 4px;
   position: absolute;

   &__content {
      padding: 48px;
      display: flex;
      align-items: center;
      flex-direction: column;

      .empty-state__icon {
         width: 200px;
         height: 200px;
         display: flex;
         align-items: center;
         border-radius: 200px;
         justify-content: center;
         background-color: #f7fafc;
         box-shadow: 0px 2px 1px #e1e3ec;

         img {
            width: 170px;
         }
      }

      .empty-state__message {
         color: #38a169;
         font-size: 1.5rem;
         font-weight: 500;
         margin-top: 0.85rem;

         @media only screen and (max-width: 768px) {
            text-align: center;
           
         }
      }

      .empty-state__help {
         color: #a2a5b9;
         font-size: 0.875rem;

         @media only screen and (max-width: 768px) {
            text-align: center;
             margin-top: 20px;
         }
      }
   }
}

.credit {
   color: #A2A5B9;
   font-size: .75rem;
   text-align: center;

   a {
      color: #444;
   }
}