@import '../../assets/styles/config.scss';

.aboutUs {
   box-sizing: border-box;
   margin-top: 40px;

   &__title {
       @include normalFont(500, 35px, 42px, #000);

       @media only screen and (max-width: 768px) {
          @include normalFont(500, 25px, 32px, #000);
       }
   }

   p {
      margin-top: 30px;
      @include normalFont(normal, 18px, 28px, #000);
      
      @media only screen and (max-width: 768px) {
         @include normalFont(500, 16px, 19px, #000);
      }
   }

   &__subtitle {
      margin-top: 30px;
   }

   ul {
      margin-top: 20px;

      li {
          @include normalFont(normal, 18px, 28px, #000);

          @media only screen and (max-width: 768px) {
             @include normalFont(500, 16px, 19px, #000);
          } 
      }
   }
}