@import '../../assets/styles/config.scss';

.home {
    width: 100%;

    &-section1 {
        width: 100%;
        position: relative;

        .swiper-pagination {
            max-width: 1140px;
            margin: 0 auto;
            padding: 0 15px;
            width: 100%;
            bottom: 50px;

            .swiper-pagination-bullet {
                width: 12px;
                height: 12px;
                background: #B0B0B0;
                opacity: 1;
                transition: all ease 0.5s;
            }

            .swiper-pagination-bullet-active {
                width: 40px;
                border-radius: 10px;
                transition: all ease 0.5s;
            }
        }
    }

    &-section2,
    &-section3,
    &-section5 {
        margin-top: 90px;
    }

    &-section4 {
        margin-top: 90px;
        background: #F9F9F9;
        padding: 130px 0 64px 0;
        box-sizing: border-box;

        &-inner {
            @include space-between;
            box-sizing: border-box;

            @media only screen and (max-width: 768px) {
                flex-direction: column-reverse;
            }

            &-left {
                @include column;
                width: 50%;
                
                @media only screen and (max-width: 768px) {
                    width: 100%;
                }

                &__title {
                    @include normalFont(bold, 60px, 70px, #000);

                    @media only screen and (max-width: 1023px) {
                        @include normalFont(bold, 40px, 50px, #000);

                    }
                }

                &__subtitle {
                    @include normalFont(500, 18px, 30px, #000);
                    margin-top: 16px;

                    @media only screen and (max-width: 1023px) {
                        @include normalFont(500, 15px, 27px, rgba(0, 0, 0, 0.488));

                    }
                }

                &__button {
                    margin-top: 72px;
                    @include normalFont(500, 20px, 24px, #fff);
                    padding: 18px 70px;
                    background: #407BFF;
                    border-radius: 10px;
                    display: inline-flex;
                    align-items: center;
                    justify-content: center;
                    text-decoration: none;
                    width: max-content;
                }
            }

            &-right {
                width: 50%;
                @include center;

                @media only screen and (max-width: 768px) {
                    width: 100%;
                }



                img {
                    width: 100%;
                    height: auto;
                    object-fit: cover;
                }

            }
        }
    }
}