.authorsBooks {
   margin-top: 40px;
   box-sizing: border-box;
   width: 100%;

   


   &-top {
      display: flex;
      align-items: center;
      justify-content: space-between;

      &__button {
         cursor: pointer;
         padding: 10px 25px;
         background: #407BFF;
         border-radius: 10px;
         color: #fff;
         border: 1px solid #407BFF;
         outline: none;

         @media only screen and (max-width: 768px) {
           margin-left: 0;
           margin-right: 10px;
           margin-top: 20px;
         }
         
         &:hover {
            background: transparent;
            color: #407BFF;
         }
         
      }
   }

   &-center {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      margin-top: 40px;

       @media only screen and (max-width: 768px) {
          align-items: center;
          flex-direction: column;
          width: 100%;
       }

      .bookBlock {
         margin-bottom: 20px;
      }
   }
}