
.dropSearch {
   position: relative;
   border: 1px solid #000;
   background: rgb(255, 255, 255);
   border-radius: 6px;
   width: 100%;

  

   .inputWrapper {
      box-sizing: border-box;
      padding: 8px 12px;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .inputWrr {
         display: flex;
         align-items: center;
         position: relative;
         width: 100%;

         .dropBox {
            justify-content: flex-start;
            display: flex;
            font-size: 0.8rem;
            font-weight: bold;
            padding: 10px;
            width: 100%;
            height: 100%;

            &.active {
               display: none;
            }
         }
      }

      input {
         display: flex;
         border: none !important;
         width: 100%;
         background: transparent;
         outline: none;
         font-style: normal;
         font-weight: normal;
         font-size: 15px;
         line-height: 18px;
         color: #000;
         letter-spacing: -0.23px;

         &::placeholder {
            opacity: 0.3;
         }
      }

      .dropSearchArrow {
         display: inline-block;
         height: 100%;
         display: flex;
         align-items: center;
         justify-content: center;
         opacity: 0.3;

         svg {
            transition: all ease 0.5s;
         }

         &.active {
            svg {
               transition: all ease 0.5s;
               transform: rotate(180deg);
            }
         }
      }
   }
}

.dropSearchDatalist {
   margin-top: 1px;
   z-index: 300;
   position: absolute;

   border-radius: 0 0 5px 5px;
   border-top: none;
   font-family: sans-serif;
   width: 100%;
   max-height: 10rem;
   overflow-y: scroll;
   box-sizing: border-box;
   display: flex;
   align-items: center;
   flex-direction: column;
   box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.679);
   padding: 0;
   transition: all 0.25s cubic-bezier(0.31, -0.105, 0.43, 1.4);

   &::-webkit-scrollbar {
      width: 2px;
      background: #407BFF;
   }

   &::-webkit-scrollbar-track {
      background: transparent;
   }

   &::-webkit-scrollbar-thumb {
      color: #fff;
   }

   span {
      width: 100%;
      display: block;
      cursor: pointer;
      background: #fff;
      color: #407BFF;
      line-height: inherit;
      padding: 10px;
      box-sizing: border-box;

      font-style: normal;
      font-weight: 500;
      font-size: 10px;
      line-height: 13px;

      // padding: 7px;
      // color: #090761;
      // margin-bottom: 1px;
      // font-size: 18px;
      // cursor: pointer;
      // display: flex;
      // align-items: center;
      &.disabled {
         opacity: 0.4;
      }

      &.selected {
         background: #ff0000;
         color: #fff;
         position: relative;

         &::before {
            content: "Selected";
            font-family: "Montserrat";
            font-size: 10px;
            position: absolute;
            padding: 0 10px;
            right: 0;
            text-align: center;
            font-weight: 500;
            background: #ff0000;
            z-index: 2;
         }

         &:hover {
            background: #ff0000;
            color: #fff;

            &::before {
               content: "Press enter to remove";
            }
         }
      }
   }

   span:hover,
   .active {
      background-color: #00ca08;
      color: #fff;
   }
}
