@import '../../assets/styles/config.scss';

.pageForm {
   position: fixed;
   width: 100%;
   height: 100vh;
   top: 0;
   right: 0;
   bottom: 0;
   left: 0;
   @include center;
   z-index: 2000;

   display: none;

   &.active {
      display: flex;
   }

   &-left {
      background: rgba(0, 0, 0, 0.356);
      width: calc(100% - 300px);
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
   }

   &-right {
      width: 300px;
      background: #fff;
      min-height: 100vh;
      max-height: 100vh;
      position: absolute;
      right: 0;
      padding: 15px 10px;
      box-sizing: border-box;
      overflow-y: scroll;
      -ms-overflow-style: none;
      scrollbar-width: none;

      &::-webkit-scrollbar {
         display: none;
      }


      &-top {
         @include space-between-center;

         &__button {
            @include center;
            position: relative;
            min-width: 20px;
            min-height: 20px;
            cursor: pointer;
            background: transparent;
            border: none;
            outline: none;

            span {
               @include center;
               position: absolute;
               width: 30px;
               height: 30px;

               &::before {
                  content: "";
                  position: absolute;
                  width: 20px;
                  height: 2px;
                  background: #000;
                  @include center;
                  transform: rotate(-45deg);
               }

               &::after {
                  content: "";
                  position: absolute;
                  width: 20px;
                  height: 2px;
                  background: #000;
                  @include center;
                  transform: rotate(45deg);
               }
            }
         }
      }

      &-center {
         margin-top: 30px;

         &-row {
            margin-bottom: 20px;

            ul {
               padding-left: 20px;
            }

            &__label {
               margin-bottom: 10px;
               display: block;
            }

            .input {

               &.type1 {
                  width: 100%;
                  border: 1px solid #000000;
                  box-sizing: border-box;
                  border-radius: 8px;
                  padding: 6px 10px;


                  input,
                  textarea {
                     border: none;
                     outline: none;
                     width: 100%;
                     resize: vertical;
                  }
               }

               &.type2 {
                  width: 100%;
                  border: 1px solid #000000;
                  box-sizing: border-box;
                  border-radius: 8px;
                  padding: 6px 10px;


                  textarea {
                     border: none;
                     outline: none;
                     width: 100%;
                     resize: vertical;
                  }

                  .type2Button {
                     width: 100%;
                     @include center;
                     border: 1px solid #407BFF;
                     box-sizing: border-box;
                     border-radius: 8px;
                     padding: 6px 10px;
                     background: #407BFF;
                     color: #fff;
                     cursor: pointer;

                     &:hover {
                        color: #407BFF;
                        background: transparent;

                     }
                  }
               }
            }
         }

         &__button {
            width: 100%;
            @include center;
            border: 1px solid #407BFF;
            box-sizing: border-box;
            border-radius: 8px;
            padding: 6px 10px;
            background: #407BFF;
            color: #fff;
            cursor: pointer;

            &:hover {
               color: #407BFF;
               background: transparent;
               
            }
       
         }
      }
   }
}