@import '../../assets/styles/config.scss';

.homeBooks {
   &-top {
      @include space-between-center;
      box-sizing: border-box;

      &__title {
         @include normalFont(500, 35px, 42px, #000);

         @media only screen and (max-width: 768px) {
            @include normalFont(500, 25px, 32px, #000);
         }
      }

      &__link {
         @include normalFont(500, 16px, 19px, #000);
         @media only screen and (max-width: 768px) {
            @include normalFont(500, 12px, 15px, #000);
         }
      }
   }

   &-center {
      margin-top: 40px;
   }
}