@import '../../assets/styles/config.scss';

.basket {
   @include space-between;
   margin-top: 40px;
   box-sizing: border-box;

   @media only screen and (max-width: 768px) {
      flex-direction: column;
   }

   &-left {
      border: 1px solid #00000025;
      box-sizing: border-box;
      border-radius: 15px;
      width: calc(100% - 285px);
      padding: 40px;
      @media only screen and (max-width: 1023px) {
         padding: 20px;
      }

      @media only screen and (max-width: 768px) {
         width: 100%;
      }

      &-header {
         @include space-between-center;

         &__title {
            @include normalFont( 700, 35px, 42px, #000);
            
            @media only screen and (max-width: 550px) {
               @include normalFont( 700, 28px, 35px, #000);

            }

            span {
               @include normalFont( 500, 16px, 19px, #000);
               opacity: 0.4;
            }
         }

         &__clear {
            background: #FF5952;
            border-radius: 10px;
            border: 1px solid #FF5952;
            padding: 6px 22px;
            color: #fff;
            cursor: pointer;
            outline: none;

            @media only screen and (max-width: 550px) {
               min-width: 35px;
               max-width: 35px;
               min-height: 35px;
               max-height: 35px;
               @include center;

            }

            &:hover {
               background: transparent;
               color: #FF5952;
            }
         }
      }



   }

   &-right {
      width: 255px;
      @media only screen and (max-width: 768px) {
         width: 100%;
         margin-top: 20px;
      }
      &-inner {
         @include column;
         position: -webkit-sticky;
         position: sticky;
         top: 120px;

      }
      &-row {
         @include space-between-center;
         &__title {
            @include normalFont(500, 32px, 42px, #000);
         }
         &__subtitle {
            @include normalFont(400, 30px, 36px, #000);
         }
      }

      &-form {
         @include column;
         &-row {
            @include column;
            margin-top: 20px;

            label {
               @include normalFont(500, 15px, 18px, #000);
            }
            
            input {
               @include normalFont(500, 17px, 18px, #000);
               padding: 5px 10px;
               border-radius: 5px;
               outline: none;
               border: 1px solid #000;
               margin-top: 4px;
            }
            
            &__button {
               @include normalFont(500, 17px, 18px, #fff);
               background: #FF5952;
               border-radius: 10px;
               border: 1px solid #FF5952;
               padding: 10px 32px;
               color: #fff;
               cursor: pointer;
               outline: none;

               &:hover {
                  background: transparent;
                  color: #FF5952;
               }
            }
         }
         
      }
   }
}