.myModal {
   position: fixed;
   width: 100%;
   height: 100vh;
   top: 0;
   bottom: 0;
   left: 0;
   right: 0;
   display: none;
   align-items: center;
   justify-content: center;
   background: #00000047;
   z-index: 2000;
   &.active {
      display: flex;
   }
   
   &-inner {
      min-width: 350px;
      min-height: 200px;
      background: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      position: relative;

      .loading {
         position: absolute;
         width: 100%;
         height: 100%;
         z-index: 2001;
         background: rgba(255, 255, 255, 0.448);
      }

      &-center {
         margin-top: 30px;

         &__no {
            background: rgb(217, 217, 217);
            &:hover {
               background: transparent;
               border: 1px solid rgb(217, 217, 217);
               color: rgb(217, 217, 217);
            }
         }

         &__yes {
            margin-left: 10px;
            background: #407BFF;
            color: #fff;
            &:hover {
               background: transparent;
               border: 1px solid #407BFF;
               color: #407BFF;
            }
         }

         button {
            border: 1px solid transparent;
            padding: 5px 25px;
            border-radius: 10px;
            cursor: pointer;
         }
      }
   }

}