@import '../../assets/styles/config.scss';

.adminBooks {
   margin-top: 90px;
   box-sizing: border-box;

   @media only screen and (max-width: 768px) {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: unset;
      width: 100%;
   }

   &-top {
      @include align-center;
      justify-content: space-between;
      box-sizing: border-box;

      @media only screen and (max-width: 768px) {
         display: flex;
         align-items: center;
         justify-content: space-between;
         width: 100%;
      }

      &__button {
         cursor: pointer;
         padding: 10px 25px;
         background: #407BFF;
         border-radius: 10px;
         color: #fff;
         border: 1px solid #407BFF;
         outline: none;
         overflow: hidden;
         white-space: nowrap;
         text-overflow: ellipsis;

         &:hover {
            background: transparent;
            color: #407BFF;
         }

         margin-left: 10px;

         @media only screen and (max-width: 768px) {
            margin: 0;
            width: calc(100% / 3 - 5px);
            padding: 10px 10px;
         }
      }
   }

   &-center {
      margin-top: 40px;
      @include space-between;
      flex-wrap: wrap;

      @media only screen and (max-width: 768px) {
         align-items: center;
         flex-direction: column;
         justify-content: unset;
         width: 100%;
      }
   }
}