@import '../../assets/styles/config.scss';
.bookBlock {
    @include column-center;
    text-decoration: none;
    padding-top: 80px;


    &:hover {
        
        .bookBlock-info {
            box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
            transition: 250ms;
        }
    }
    &-img {
        width: 145px;
        height: 225px;
        position: absolute;
        transform: translateY(-30%);
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        background: url(../../assets/Images/loadingSmall.gif);
        background-position: center;
        background-repeat: no-repeat;

    
        
        
        img{
            width: 100%;
            height: 100%;
            position: absolute;
            object-fit: cover;
        }
    }
    &-info {
        transition: all ease 0.5s;
        min-height: 300px;
        min-width: 255px;
        max-width: 255px;
        border: 1px solid rgba(0, 0, 0, 0.1);
        box-sizing: border-box;
        border-radius: 10px;
        padding: 0 16px;
        padding-bottom: 16px;
        padding-top: 181px;

        &__link {
            text-decoration: none
        }


        &__title {
            @include normalFont(normal, 18px, 28px, #000);
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
        
        &__subtitle {
            @include normalFont(500, 16px, 19px, #000);
            margin-top: 17px;
            opacity: 0.5;
            min-height: 38px;
            display: inline-flex;
        }

        &-row {
            @include space-between-center;
            margin-top: 17px;
            &__price {
                del {
                    opacity: 0.5;
                    font-size: 14px;

                }
                @include normalFont(500, 22px, 26px, #000);
            }

           

            &__view {
                opacity: 0.5;
                @include normalFont(500, 18px, 22px, #000);
                @include center;
                &--count {
                    margin-left: 10px;
                }

            
            }

            &__share {
                border: none;
                outline: none;
                @include center;
                background: transparent;
                cursor: pointer;

                
            }

            &__button {
                background: #F7F8F8;
                border-radius: 10px;
                border: 4px solid transparent;
                box-sizing: border-box;
                outline: none;
                padding: 15px 32px;
                cursor: pointer;
                
                svg {
                    width: 21px;
                    height: 21px;
                }
                
                &:hover {
                    border: 4px solid #F7F8F8;
                    background: transparent;
                }

                

            }
        }

    }
}
