@import '../../assets/styles/config.scss';

.basketBlock {
   @include space-between;
   border-bottom: 1px solid #00000025;
   margin-top: 20px;
   padding: 20px 0;

   @media only screen and (max-width: 550px) {
     flex-direction: column;
   }
   &-left {
      display: flex;
      &-img {
         min-width: 101.5px;
         max-width: 101.5px;
         min-height: 157.5px;
         max-height: 157.5px;
         position: relative;
         box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
         background: url(../../assets/Images/loadingSmall.gif);
         background-position: center;
         background-repeat: no-repeat;
         margin-right: 22px;

         img {
            width: 100%;
            height: 100%;
            position: absolute;
            object-fit: cover;
         }
      }

      &-info {
         @include column;

         &__title {
            @include normalFont(400, 18px, 28px, #000);
         }
         
         &__subtitle {
            @include normalFont(400, 16px, 19px, #000);
            opacity: 0.4;

         }

      }
   }

   &-right {
      @include column;
      justify-content: space-between;

      @media only screen and (max-width: 550px) {
         margin-top: 20px;
         flex-direction: row-reverse;
      }
      &-counter {
         @include center;

         &__button {
            width: 40px;
            height: 40px;
            @include center;
            background: #F7F8F8;
            border-radius: 50%;
            border: none;
            outline: none;

            &:disabled {
               opacity: 0.4;
               background: #FF5952;
               svg {
                  fill: #fff;
               }
            }

            &:hover {
               background: #FF5952;
               cursor: pointer;

               svg {
                  fill: #fff;
               }
            }
         }

         &__title {
            margin: 0 25px;
         }
      }

      &__price {
         display: flex;
         justify-content: flex-end;
         @media only screen and (max-width: 550px) {
            @include center;
         }
      }

      &__delete {
         border: 1px solid transparent;
         outline: none;
         cursor: pointer;
         padding: 5px 0;
         border-radius: 10px;

         @media only screen and (max-width: 550px) {
            width: 35px;
            height: 35px;
            @include center;   
         }

         svg {
            fill: #000;
            opacity: 0.4;
         }
         
         &:hover {
            svg {
               fill: red;
               opacity: 1;
            }
         }

      }
   }
}