.AuthorCard {
   background: rgb(69, 69, 69);
   border-radius: 5px;
   display: flex;
   align-items: center;
   min-width: 255px;
   max-width: 255px;
   min-height: 75px;
   max-height: 75px;
   padding: 10px;
   box-sizing: border-box;
   text-decoration: none;
   margin-right: 11px;
   margin-bottom: 15px;
   @media only screen and (max-width: 768px) {
      min-width: 100%;
      max-width: 100%;
   }

   &-img {
      min-width: 54px;
      max-width: 54px;
      min-height: 54px;
      max-height: 54px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgb(255, 255, 255);
      border-radius: 8px;

      &__title {
         min-width: 34px;
         max-width: 34px;
         min-height: 34px;
         max-height: 34px;
         display: flex;
         align-items: center;
         justify-content: center;
         color: #000;
         border-radius: 50%;
         overflow: hidden;
      }
   }

   &-info {
      margin-left: 6px;

      &__title {
         font-style: normal;
         font-weight: 500;
         font-size: 17px;
         line-height: 22px;
         letter-spacing: -0.43px;
         color: #fff;
      }

      &__subtitle {
         font-style: normal;
         font-weight: 500;
         font-size: 11px;
         line-height: 14px;
         letter-spacing: 0.06px;
         // color: $blueColor;
      }
   }
}
