.authorBlock {
  display: flex;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  box-sizing: border-box;
  padding: 10px;
  border-radius: 10px;
  width: 350px;
  text-decoration: none;
  color: #000;

  @media only screen and (max-width: 768px) {
    width: 100%;
  }

  &-img {
    max-width: 80px;
    min-width: 80px;
    max-height: 80px;
    min-height: 80px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }

  &-info {
    margin-left: 20px;
    display: flex;
    align-items: center;

    &__title {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }
}