.search {
   box-sizing: border-box;
   display: flex;
   flex-direction: column;

   &-top {
      display: flex;
      margin-top: 60px;
      background: #F9F9F9;
      border-radius: 4px;
      overflow: hidden;
      padding: 0;


      input {
         border: 3px solid #FFC727;
         width: calc(100% - 94px);
         outline: none;
         font-size: 18px;
         padding: 10px 20px;
      }

      &__button {
         cursor: pointer;
         padding: 10px 25px;
         background: #FFC727;
         color: #fff;
         border: 3px solid #FFC727;
         border-left: none;
         outline: none;
         margin: 0;



         &:hover {
            background: transparent;
            color: #000;
         }
      }
   }

   &-center {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      margin-top: 60px;
      position: relative;
      min-height: 400px;

      @media only screen and (max-width: 768px) {
         align-items: center;
         flex-direction: column;
         justify-content: unset;
         width: 100%;
      }

      .bookBlock {
         margin-bottom: 20px;
      }
   }

   &-bottom {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 30px;

      &__button {
         cursor: pointer;
         padding: 10px 25px;
         background: #407BFF;
         border-radius: 10px;
         color: #fff;
         border: 1px solid #407BFF;
         outline: none;

         &:hover {
            background: transparent;
            color: #407BFF;
         }
      }
   }
}